.library-main .ant-select {
  margin-right: 25px;
}
.library-main-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
}
.library-main-content {
  margin-top: 19px;
  background: #f8f8f8;
  border-radius: 3px;
  padding: 15px 20px;
  width: calc(50% - 15px);
  margin-right: 30px;
  font-family: Alibaba PuHuiTi;
}
.library-main-content p {
  margin: 0;
}
.library-main-content h2 {
  margin: 0;
}
.library-main-content span {
  color: #888;
  font-size: 14px;
  font-family: Alibaba PuHuiTi;
  display: inline-block;
  width: 50%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.library-main-content .homework-name {
  width: 480px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: middle;
  color: #2A283D;
  font-size: 16px;
  margin-bottom: 10px;
  line-height: 100%;
}
.library-main-content .data {
  display: flex;
  justify-content: flex-end;
}
.library-main-content .data div {
  text-align: center;
  cursor: pointer;
  width: 30%;
}
.library-main-content .data div h2 {
  color: #2A283D;
  font-weight: 500;
  font-family: Alibaba PuHuiTi;
  margin-bottom: 5px;
}
.library-main-content .data div p {
  color: #2A283D;
  font-family: Alibaba PuHuiTi;
}
.library-main-content .data div:nth-of-type(2) h2,
.library-main-content .data div:nth-of-type(2) p {
  color: #3E78ED;
}
.library-main-content:nth-child(2n) {
  margin-right: 0;
}
.homework-wrap .title {
  font-size: 28px;
  color: #2A283D;
  padding: 30px 20px;
  font-weight: 500;
}
.homework-wrap .content {
  background: #fff;
  padding: 16px 30px 60px;
  min-height: calc(100vh - 215px);
  position: relative;
}
.homework-wrap .content .pagination-style-l {
  width: 100%;
  bottom: -50px;
}
.homework-wrap .content .content-top .ant-select-selection {
  background: #f4f4f4;
  width: 188px;
  height: 30px;
  line-height: 28px;
  border-radius: 2px;
}
.homework-wrap .content .ant-select-selection__rendered {
  line-height: 30px;
}
.homework-wrap .content .ant-pagination {
  position: absolute;
  bottom: 60px;
  right: 15px;
}
